import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ROUTING } from './app.routing';

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HeaderComponent } from './common/header/header.component';
import { GridComponent } from './grid/grid.component';
import { BudgetDashboardComponent } from './dashboard/budget-dashboard/budget-dashboard.component';
import { QcbdChartComponent } from './dashboard/qcbd-chart/qcbd-chart.component';
import { FooterComponent } from './common/footer/footer.component';
import { ConversionComponentComponent } from './conversion-component/conversion-component.component';
import { MainDashboardComponent } from './main-dashboard/main-dashboard.component';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";


@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    HeaderComponent,
    GridComponent,
    BudgetDashboardComponent,
    QcbdChartComponent,
    FooterComponent,
    ConversionComponentComponent,
    MainDashboardComponent
  ],
  imports: [
    BrowserModule,
    ROUTING,
    BrowserAnimationsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



